.dashboard-main {
    margin-top: 1vw;
    margin-left: 1vw;
    margin-right: 1vw;
    padding: 10px 10px;
    border-radius: 5px;
    background-color: #fff;
    box-shadow:  1px 3px 0 rgba(0, 0, 0, 0.2), 0 1px 1px 0 rgba(0, 0, 0, 0.14), 0 2px 1px -1px rgba(0, 0, 0, 0.12);
    -webkit-border-radius: 5px;
    -moz-border-radius: 5px;
    -ms-border-radius: 5px;
    -o-border-radius: 5px;
    /* margin-bottom: 3vw;   */
}
.dashboard-head4{
    margin-top: 1vw;
    font-size: 12px;
}
.contact-detail-form{
    display: flex;
    flex-wrap: wrap;
    gap:0 10px;
    margin-top: 1vw;
}
.contact-detail-form>.ant-col{
    flex:0 1 30%;

}
.form-item-name{
    margin-bottom: 5px;
}
.export-button{
    padding-left: 1px;
}
.export-btn{
    color: #fff;
    background-color: orangered;
}